
body {
    font-family: 'PT Sans';
}

h1, h2, h3, h4, h5 {
    font-family: 'Concert One';
}

.container {
  margin: auto;
  max-width: 980px;
  border: 10px solid white;
  background-color: rgba(255, 255, 255, 0.75);
  max-height: 600px;
  overflow: auto;
}

.icon-large {
  font-size: 32px;
}

a {
  text-decoration: none;
  color: darkslategray;
}

.clear {
  clear: both;
}

.header {
  width: 100%;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
}

.toolbar {
  padding-bottom: 10px;
  bottom: 0;
  text-align: center;
  width: 100%;
}

.toolbar-logo {
  float: left;
}

.toolbar-buttons {
  float: right;
  padding-top: 20px;
  padding-bottom: 20px;
}

.menu-button {
  margin-left: 10px;
  margin-right: 10px;
  width: 100px;
  height: 100px;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 50%;
  display: inline-block;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
}

.menu-button-content {
  margin-top: 20px;
}

.menu-button .text {
  font-family: 'Playfair Display';
  font-weight: bold;
  font-size: 14px;
  color: black;
  padding: 5px 15px;
}

.menu-button:hover {
  background-color: black;
}

.menu-button:hover .text {
  color: white;
}

.sidebar {
  margin-top: -15px;
  padding-top: 0px;
  padding-left: 5px;
  text-align: left;
}

.sidebar-icon {
  display: inline-block;
  width: 45px;
  height: 35px;
  padding: 0px;
  cursor: pointer;
  border-radius: 10%;
  margin-right: 10px;
  vertical-align: top;
}

.sidebar-icon:hover {
  background-color: slategray;
}


.sidebar-icon-text {
  text-align: center;
  font-weight: normal;
  font-family: "Playfair Display";
  font-size: 12px;
  margin-top: 3px;
  color: black;
}

.bg-red {
  background-color: darkred;
}

.bg-teal {
  background-color: teal;
}

.bg-cyan {
  background-color: cyan;
}

.bg-blue {
  background-color: slateblue;
}

.bg-yellow {
  background-color: lightgoldenrodyellow;
}

.bg-gold {
  background-color: goldenrod;
}

.bg-pink {
  background-color: lightcoral;
}

.about {
  padding: 40px;
  font-size: larger;
}

.about>div {
  display: inline;
}

.indent {
  padding-left: 20px;
}

.indent-right {
  padding-right: 20px;
}

.border-right {
  border-right: 1px solid black;
}

.border-left {
  border-left: 1px solid black;
}

.middle {
  vertical-align: middle;
}

.center {
  text-align: center;
}

.weare501 {
  padding: 5px;
  background-color: black;
  width: 96%;
}

.weare501 > .text {
  padding: 5px;
  font-size: 11px;
  color: gold;
  border: 1px solid goldenrod;
}

.adopt {
  padding: 40px;
  font-size: larger;
}

.ourpets {
  display: flex;
  flex-wrap: wrap;
  background-color: black;
}

.pet {
  padding: 10px;
}

.petname {
  font-family: 'Concert One';
  font-size: 20px;
  text-align: center;
  background-color: white;
  color: teal;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
